@import '../../sharedStyles/mixins.scss';
@import '../../sharedStyles/variables.scss';

.dish-page {
  display: flex;
  justify-content: center;
  @include marginBothSides;
  width: 80%;
  @include flexColumn;
  color: $color-gray;
  align-items: center;
  margin: auto;
  @include marginTop;

  .dish-img {
    border-radius: 20%;
    margin-top: 15px;
  }

  .dish-price {
    text-align: center;
    font-size: 20px;
    color: $color-black;
    margin-top: 20px;
  }
  .dish-title {
    color: $color-black;
    width: 70%;
    font-weight: bold;
    font-size: 29px;
    margin-top: 10px;
    text-align: center;
    letter-spacing: 0.2px;
    display: flex;
    background-color: $color-pink-button;
  }
  .dish-des {
    margin-top: 15px;
    color: $color-black;
    font-weight: bold;
    text-align: center;
    letter-spacing: 0.1px;
    background-color: $color-gray-light;
  }

  .changes {
    transform: translateY(-10px);
    align-items: center;
    margin: auto;
  }

  .shopping-btn {
    cursor: pointer;
    &:hover {
      transform: translateY(-2px);
    }
  }
}
