@import '../../sharedStyles/mixins.scss';
@import '../../sharedStyles/variables.scss';

.drinks-content {
  @include marginTop;
  @include marginBothSides;

  .drinks-title {
    @include title;
  }

  .drinks-subtitle {
    font-weight: 600;
    text-decoration: underline;
    font-size: 18px;
    margin-bottom: 10px;
    margin-top: 20px;
  }

  .drinks {
    @include flexColumn;
    gap: 20px;
  }

  .drink {
    @include flexRow;
    justify-content: space-between;
    align-items: center;
    // width: 100%;

    &:last-child {
      margin-bottom: 20px;
    }
  }

  .addDrink {
    background-color: $color-pink-button;
    border: none;
    padding: 7px 15px;
    border-radius: 7px;
    font-weight: 500;
    font-size: 16px;
    cursor: pointer;

    &:hover {
      background-color: $color-gray-light;
    }
  }

  .drink-price {
    position: absolute;
    left: 50%;
    transform: -50%;
    z-index: 0;
  }
}
