@import '../../sharedStyles/mixins.scss';
@import '../../sharedStyles/variables.scss';

.recommendation {
  @include flexColumn;
  gap: 10px;
  justify-content: center;
  align-items: center;
  font-size: 18px;
  margin-bottom: 25px;

  .left {
    @include flexRow;
    justify-content: space-between;
    align-items: center;
    gap: 10px;
  }

  .rec-img {
    width: 350px;
    height: 180px;
    border-radius: 20px;
  }

  .rec-time-icon {
    font-size: 25px;
  }

  .rec-time {
    @include flexRow;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    font-weight: 600;
  }

  .rec-price {
    font-weight: 700;
  }

  .rec-time-estimate {
    font-weight: 700;
  }

  .rec-details {
    @include flexRow;
    gap: 20px;
    justify-content: space-between;
    align-items: center;

    .rec-title {
      padding: 10px;
      background-color: $color-pink-button;
      border-radius: 10px;
    }
  }

  .rec-description {
    padding: 10px;
    background-color: $color-gray-light;
    border-radius: 10px;
    width: 350px;
    text-align: center;
  }
}

.icon {
  width: 45px;
  height: 40px;
  background: none;
  background-color: none;
}

.icons {
  display: flex;
  gap: 8px;
  justify-content: flex-start;
  align-items: flex-start;
}
