@import '../../sharedStyles/mixins.scss';
@import '../../sharedStyles/variables.scss';

.login {
  @include flexColumn;
  color: $color-gray;
  align-items: center;
  margin: auto;
  @include marginTop;
  @include marginBothSides;

  .login-title {
    color: $color-pink1;
    width: 76%;
    font-weight: bold;
    font-size: 35px;
    text-align: center;
    letter-spacing: 0.1px;
  }

  .login-subtitle {
    font-size: 16px;
  }

  .login-form {
    font-size: 20px;
    margin-top: 40px;
    width: 100%;
    text-align: center;

    @include flexColumn;
    gap: 20px;

    .row {
      @include flexColumn;
      gap: 7px;
    }

    .expCvv {
      @include flexRow;
      align-items: center;
      margin-bottom: 20px;
      gap: 0px;

      justify-content: space-between;
      width: 100%;

      input {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 100%;
        align-self: center;
      }
    }

    .halfRow {
      @include flexColumn;
    }

    label {
      letter-spacing: 0.5px;
    }

    input {
      font-size: 16px;
      font-family: inherit;
      text-align: center;
      background-color: $color-gray-light;
      border: none;
      border-radius: 20px;
      height: 30px;
      padding: 5px 0px;
    }
  }

  .login-title-sensitive {
    letter-spacing: 0.5px;
  }

  .login-note {
    color: $color-notes;
    margin-top: 20px;
    margin-bottom: 20px;

    font-weight: bold;
    text-align: center;
  }

  .invalid-input {
    color: $color-notes;
    font-size: 16px;
  }

  .sensitives {
    @include flexColumn;
    gap: 10px;
    margin-top: 20px;
  }

  .button-sensitive {
    width: 150px;
    padding: 5px;
    cursor: pointer;
    border: 2px solid $color-pink-button;
    background-color: $color-white;
    border-radius: 20px;
    font-family: inherit;
    letter-spacing: 0.4px;
    font-size: 16px;

    &:hover {
      background-color: $color-pink-button;
    }
  }

  .button-active {
    width: 150px;
    padding: 5px;
    cursor: pointer;
    border: 2px solid $color-pink-button;
    background-color: $color-white;
    border-radius: 20px;
    font-family: inherit;
    letter-spacing: 0.4px;
    background-color: $color-pink-button;
  }
}

.title {
  flex-grow: 1;
}

.goBack {
  align-self: flex-start;
}
