@import '../../sharedStyles/variables.scss';
@import '../../sharedStyles/mixins.scss';

.button {
  background-color: $color-pink-button;
  border-radius: 25px;
  color: $color-white;
  border: none;
  padding: 7px;
  font-size: 22px;
  cursor: pointer;
  @include flexRow;
  justify-content: center;
  align-items: center;
  height: 55px;

  &:hover {
    background-color: $color-white;
    border: 3px solid $color-pink-button;
    color: black;
  }
}
.disable {
  cursor: auto;
  background-color: #e8dfdf;
  border-radius: 25px;
  border: 1px solid black;
  padding: 7px;
  font-size: 22px;
  @include flexRow;
  justify-content: center;
  align-items: center;
  height: 55px;
}

.title {
  flex-grow: 1;
}
