@mixin center {
  display: flex;
  justify-content: center;
  align-items: center;
}

@mixin flexColumn {
  display: flex;
  flex-direction: column;
  @content;
}

@mixin flexRow {
  display: flex;
  flex-direction: row;
  @content;
}

@mixin marginBothSides {
  margin-left: 25px;
  margin-right: 25px;
}

@mixin MarginSmartphone {
  width: calc(100% - 48px);
}

@mixin disableButtom {
  background-color: gray;
  border: gray;
  cursor: none;
}

@mixin marginTop {
  margin-top: 80px;
}

@mixin title {
  color: $color-gray;
  font-weight: 700;
  font-size: 30px;
  letter-spacing: 0.3px;
  text-transform: uppercase;
  text-align: center;
  margin-bottom: 20px;
}
