@import '../../sharedStyles/mixins.scss';
@import '../../sharedStyles/variables.scss';

.bill {
  @include flexColumn;
  color: $color-gray;
  align-items: center;
  margin: auto;
  position: relative;
  z-index: 1;

  .adds ul {
    display: flex;
    flex-direction: column;
    gap: 20px; /* adjust the gap value to your liking */
  }

  .adds div {
    display: flex;
    align-items: center;
  }

  .askforbill {
    position: fixed;
    bottom: 0px;
    /* other styles */
  }
}

.special-request {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

textarea {
  width: 100%;
}

.time {
  font-size: 20px;
  font-weight: 700;
  color: black;
  margin-top: 50px;
  width: 60%;
}
