@import '../../sharedStyles/mixins.scss';
@import '../../sharedStyles/variables.scss';

a {
  cursor: pointer;
}
.navigation {
  position: fixed;
  margin-top: 0;
  top: 0;
  left: 0;
  display: flex;
  align-items: center;
  // justify-content: space-between;
  height: 40px;
  width: 100%;
  padding: 0.5rem 0rem;
  background-color: $color-pink-button;
  color: black;
  border-bottom: 1px solid black;
  z-index: 100;
}

.toCart_btn {
  @include flexRow;
  align-items: center;
  justify-content: center;
  border: 2px solid black;
  background-color: transparent;
  padding: 2px;
  border-radius: 5px;
  gap: 4px;
  font-size: 20px;
  // margin-left: 20px;
  font-weight: 700;
  cursor: pointer;

  &:hover {
    background-color: $color-gray;
  }
}

a {
  text-decoration: none;
  color: black;
  font-weight: 600;

  // &:hover {
  //   text-decoration: underline;
  //   border-bottom: 1px solid black;
  // }
}

.goBack {
  text-decoration: none;
  color: black;
  font-size: 1.3rem;
  margin-left: 1rem;
  transition: transform 0.2s ease-out;
  font-size: 23px;

  &:hover {
    transform: translateY(-2px);
  }
}

.navigation-menu {
  margin-left: auto;
}

.navigation-menu ul {
  display: flex;
  padding: 0;
  margin-top: -3px;
}

.navigation-menu li {
  list-style-type: none;
  margin: 0 1rem;
}

.navigation-menu li a {
  text-decoration: none;
  display: block;
  width: 100%;
}

.hamburger {
  border: 0;
  height: 40px;
  width: 40px;
  padding: 0.5rem;
  border-radius: 50%;
  background-color: $color-gray-light;
  cursor: pointer;
  transition: background-color 0.2s ease-in-out;
  position: absolute;
  top: 50%;
  right: 25px;
  transform: translateY(-50%);
  display: none;
}

.hamburger:hover,
.hamburger:active {
  background-color: $color-gray;
}

.bell,
.billIcon {
  background-color: transparent;
  border: none;
  font-size: 23px;
  margin-left: 20px;
  cursor: pointer;
  transition: transform 1s ease-out;

  &:hover {
    transform: translateY(-2px);
  }
}

.bellIcon {
  margin-left: 0px;
}

@media screen and (min-width: 555px) {
  .navigation {
    position: flex;
    justify-content: space-between;
  }

  .left,
  .right {
    @include flexRow;
    // align-items: center;
    justify-content: space-between;
  }

  .bell:hover {
    border-bottom: none;
  }
  a:hover {
    border-bottom: 1px solid black;
  }

  .toCart_btn:hover {
    border-bottom: 0px solid black;
    text-decoration: none;
  }
}

@media screen and (max-width: 550px) {
  a:hover {
    text-decoration: none;
  }
  .hamburger {
    display: block;
  }

  .goBack:hover {
    text-decoration: none;
  }

  .navigation-menu ul {
    flex-direction: column;
    position: absolute;
    top: 60px;
    left: 0;
    width: 100%;
    // height: calc(100vh - 600px);
    background-color: rgb(255, 255, 255);
    // background-color: rgba(255, 255, 255, 0.539);
    border-bottom: 1px solid black;

    display: none;
    // &:hover {
    //   text-decoration: underline;
    // }
  }

  .navigation-menu li {
    text-align: center;
    margin: 0;
  }

  .navigation-menu li a {
    color: black;
    width: 100%;
    padding: 1.5rem 0;
    text-decoration: none;
  }

  .navigation-menu li:hover {
    background-color: #eee;
  }

  .navigation-menu.expanded ul {
    display: block;
  }

  .bell,
  .billIcon {
    background-color: transparent;
    border: none;
    font-size: 23px;
    // margin-left: 20px;
    cursor: pointer;
    transition: transform 1s ease-out;

    &:hover {
      transform: translateY(-2px);
    }
  }

  .billIcon {
    font-size: 30px;
    margin-left: 0;
  }

  .toCart_btn {
    @include flexRow;
    align-items: center;
    justify-content: center;
    border: 2px solid black;
    background-color: transparent;
    padding: 2px;
    border-radius: 5px;
    gap: 4px;
    font-size: 20px;
    // margin-left: 20px;
    font-weight: 700;
    cursor: pointer;

    &:hover {
      background-color: $color-gray;
    }
  }
}

.left {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 20px;
}
